import React from 'react';
import Day from './Day';
import { connect } from 'react-redux';

const Rutine = (props) => {
    const { rutine } = props;
    
    return (
        <div>
            {rutine.days.map(function (day, idx) {
                return (
                    <Day key={idx} day={day.name} imageURL={day.image} />
                )
            })}
        </div>
    )
}

const mapStateToProps = state => ({
    rutine: state.rutine
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(Rutine);
