import React, { useState } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link, Redirect } from 'react-router-dom';

const Login = () => {

    const [form, setValues] = useState({
        email: '',
        password: '',
    });
    const [loggedIn, setLoggedIn] = useState(false)

    const handleInput = e => {
        setValues({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const sendForm = () => {
        // fetch('https://some/url', {
        //     method: 'POST',
        //     body: JSON.stringify({
        //         name: this.state.name,
        //         email: this.state.email,
        //         message: this.state.message
        //     })
        // })
        //     .then(res => res.json())
        //     .catch(err => console.log(err));
        if (form.email == "fede" && form.password == "1234") {
            setLoggedIn(true);
            localStorage.setItem("user", true);
        }
    }

    return (
        loggedIn ? <Redirect to="/"/> : 
        (
        <div>
            <form>
                <p className="h4 text-center py-4 grey-text">Iniciar sesión</p>
                <div className="grey-text">
                    <MDBInput
                        label="Email"
                        icon="envelope"
                        group
                        type="email"
                        validate
                        error="wrong"
                        success="right"
                        name="email"
                        value={form.email}
                        onInput={handleInput}
                    />
                    <MDBInput
                        label="Contraseña"
                        icon="key"
                        group
                        type="password"
                        validate
                        error="wrong"
                        success="right"
                        name="password"
                        value={form.password}
                        onInput={handleInput}
                    />
                </div>
                <div className="text-center py-4 mt-3">
                    <MDBBtn color="success" onClick={sendForm}>Ingresar</MDBBtn>
                    <Link to='/registrarse'><MDBBtn color="primary">Registrarse</MDBBtn></Link>
                </div>
            </form>
        </div>
        )
    );
}

export default Login;
