import React, { useState } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link } from 'react-router-dom';

const Signup = () => {

    const [form, setValues] = useState({
        complete_name: '',
        cellphone: '',
        age: '',
        weight: '',
        height: '',
        email: '',
        password: '',
        administrator: false
    });

    const handleInput = e => {
        setValues({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const sendForm = () => {
        // fetch('https://some/url', {
        //     method: 'POST',
        //     body: JSON.stringify({
        //         name: this.state.name,
        //         email: this.state.email,
        //         message: this.state.message
        //     })
        // })
        //     .then(res => res.json())
        //     .catch(err => console.log(err));
        alert("Signup!!")
    }

    return (
        <form>
            <p className="h4 text-center py-4 grey-text">Registro</p>
            <div className="grey-text">
                <MDBInput
                    label="Nombre y apellido"
                    icon="file-signature"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    name="complete_name"
                    value={form.complete_name}
                    onInput={handleInput}                    
                />
                <MDBInput
                    label="Email"
                    icon="envelope"
                    group
                    type="email"
                    validate
                    error="wrong"
                    success="right"
                    name="email"
                    value={form.email}
                    onInput={handleInput}
                />
                <MDBInput
                    label="Contraseña"
                    icon="key"
                    group
                    type="password"
                    validate
                    error="wrong"
                    success="right"
                    name="password"
                    value={form.password}
                    onInput={handleInput}
                />
                <MDBInput
                    label="Celular"
                    icon="mobile-alt"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="cellphone"
                    value={form.cellphone}
                    onInput={handleInput}
                />
                <MDBInput
                    label="Edad"
                    icon="file-signature"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="age"
                    value={form.age}
                    onInput={handleInput}
                />
                <MDBInput
                    label="Peso"
                    icon="weight"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="weight"
                    value={form.weight}
                    onInput={handleInput}
                />
                <MDBInput
                    label="Altura"
                    icon="long-arrow-alt-up"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="height"
                    value={form.height}
                    onInput={handleInput}
                />                
            </div>
            <div className="text-center py-4 mt-3">
                <Link to='/login'><MDBBtn color="deep-orange">Atrás</MDBBtn></Link>          
                <MDBBtn color="success" onClick={sendForm}>Registrarse</MDBBtn>
            </div>
        </form>
    );
}

export default Signup;
